import React from 'react';
import { Link } from 'react-router-dom';
import Login from '../pages/Login/Login';
// import Dashboard from '../pages/Dashboard/Dashboard';

export default function Navbar() {
  return (
    <header>
      <nav className="navbar navbar-expand-lg  navbar-dark">
        <div className="container">
          <h2 className='text-white'>Events<span> ____</span></h2>
          <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
            <span className="navbar-toggler-icon"></span>
          </button>
          <div className='text-center'>
            <div className="collapse navbar-collapse" id="navbarSupportedContent">
              <ul className="navbar-nav me-auto mb-2 mb-lg-0">
                <li className="nav-item">
                  <Link to="/" className="nav-link active" aria-current="page">Home</Link>
                </li>
                <li className='nav-item'>
                  <Link to="/about" className='nav-link active'>About</Link>
                </li>

                {/* <li className='nav-item'>
                  <Link to="/service" className='nav-link active'>Fun</Link>
                </li> */}
                <li className='nav-item'>
                  <Link to="/product" className='nav-link active'>Product</Link>
                </li>
                <li className='nav-item'>
                  <Link to="/contact" className='nav-link active'>Contact</Link>
                </li>

               {/* <Link id='navLogin' to="/dashboard" type='login' className="nav-link active btn btn-warning mx-2">Dashboard</Link> */}
                
                <Link id='navLogin' to="/login" type='login' className="nav-link active btn btn-warning mx-2">Login</Link>

                

              </ul>
            </div>
          </div>
        </div>
      </nav >
    </header >


  )
}
