import React from 'react';

export default function Contact() {

   
    
  
    return (
        <>





        <div className='container'>
       
        <div className="row">
                <div className="col-12">
                <h2 className="contact-title text-center my-4"> <b className='text-center'>Get in Touch</b> </h2>
                 
                </div>
                <div className="col-lg-8">
                    <form className="form-contact contact_form" action="contact_process.php" method="post" id="contactForm" novalidate="novalidate">
                        <div className="row">
                            <div className="col-12">
                                <div className="form-group">
                                    <textarea className="form-control w-100" name="message" id="message" cols="30" rows="9" onfocus="this.placeholder = ''" onblur="this.placeholder = 'Enter Message'" placeholder=" Enter Message"></textarea>
                                </div>
                            </div>
                            <div className="col-sm-6">
                                <div className="form-group">
                                    <input className="form-control valid my-2" name="name" id="name" type="text" onfocus="this.placeholder = ''" onblur="this.placeholder = 'Enter your name'" placeholder="Enter your name"/>
                                </div>
                            </div>
                            <div className="col-sm-6">
                                <div className="form-group">
                                    <input className="form-control valid my-2" name="email" id="email" type="email" onfocus="this.placeholder = ''" onblur="this.placeholder = 'Enter email address'" placeholder="Email"/>
                                </div>
                            </div>
                            <div className="col-12">
                                <div className="form-group">
                                    <input className="form-control my-2" name="subject" id="subject" type="text" onfocus="this.placeholder = ''" onblur="this.placeholder = 'Enter Subject'" placeholder="Enter Subject"/>
                                </div>
                            </div>
                        </div>
                        <div className="form-group mt-3 mb-5">
                            <button type="submit" className="btn btn-success w-25 justify-content-center">Send</button>
                        </div>
                    </form>
                </div>
                <div className="col-lg-3 offset-lg-1">
                    <div className="media contact-info">
                        <i id='fontcont' class="fa-solid fa-house"></i>
                        <div className="media-body">
                            <h3>Buttonwood, California.</h3>
                            <p>Rosemead, CA 91770</p>
                        </div>
                    </div>
                    <div className="media contact-info">
                        <i id='fontcont' class="fa-sharp fa-solid fa-mobile"></i>
                        <div className="media-body">
                            <h3>+1 253 565 2365</h3>
                            <p>Mon to Fri 9am to 6pm</p>
                        </div>
                    </div>
                    <div className="media contact-info">
                        <i id='fontcont' class="fa-sharp fa-solid fa-facebook-messenger"></i>
                        <div className="media-body">
                            <h3>Email#160;protected</h3>
                            <p>Send us your query anytime!</p>
                        </div>
                    </div>
                </div>
            </div>
        

        </div>
                 </>
)
}
