import React, { useState } from 'react';
import { getAuth, signInWithEmailAndPassword, onAuthStateChanged, signOut } from "firebase/auth";
import { Link, Navigate, useNavigate } from "react-router-dom";
import { auth } from '../../../config/firebase';
import { useEffect } from 'react';
// import {Link , useNavigate} from 'react-router-dom';

const initialState = { email: "", password: "" }
export default function Login() {
  const [IsPasswordShow, setIsPasswordShow] = useState(false)
  const [showBtn, setShowBtn] = useState(true)
  // const navigate = useNavigate()

  const togglePassword = (e) => {
    e.preventDefault()
    setIsPasswordShow(!IsPasswordShow)
    setShowBtn(!showBtn)
  }

  const [state, setState] = useState(initialState);
  const [user, setUser] = useState({});
const navigate = useNavigate()

  useEffect(() => {
    onAuthStateChanged(auth, (user) => {
      if (user) {
        // User is signed in, see docs for a list of available properties
        // https://firebase.google.com/docs/reference/js/firebase.User
        // const uid = user.uid;
        console.log(user);
        setUser(user)

        // ...
      } else {
        // User is signed out
        // ...
      }
    });
  })

  const handleChange = e => {
    setState({ ...state, [e.target.name]: e.target.value })
  }
  const handleSubmit = (e) => {
    e.preventDefault()
    const { email, password } = state
    signInWithEmailAndPassword(auth, email, password)
      .then((userCredential) => {
        // Signed in 
        const user = userCredential.user;
        window.toastify("User Loggedin Successful");
        // navigate("/dashboard")
        console.log(userCredential)
        console.log(user)
        // ...
      })
      .catch((error) => {
        window.toastify("Somethng Went Wrong  OR Wrong Password", "error")
        console.log(error)
      });

    console.log(state)
  }

  const SignOut = (() => {

    console.log("User Has been sighn out")
    signOut(auth)
      .then(() => {
        console.log("User Logged Out");
        setUser({});
        window.toastify("Logged Out")

      })
      .catch((err) => {
        console.log(err)
      })
  })

  return (
    <>
      <div className='login my-5'>
        <div className='container align-item-center justify-centent-center'>
          {user.email
            ? <div className='container text-center my-3'>
              <h2 className='text-center'>Email : {user.email}</h2>
              <button className='btn btn-danger text-center' onClick={SignOut} >LogOut</button>
            </div>
            
            : <div className='col-12 col-md-8 offset-md-2 col-lg-6 offset-lg-3'>
              <div className='card p-2 p-md-3 p-lg-2'>
                <h1 className='text-center'>Login </h1>
                <form >
                  <div className='mb-3'>
                    <label htmlFor="form-control"></label>
                    <input className='form-control p-2' type="text" placeholder='Email' name='email' onChange={handleChange} />
                  </div>
                  <div className="input-group mb-3">
                    < input name='password' className='form-control p-2' type={IsPasswordShow ? "text" : "password"} placeholder='Password' id='password-btn' onChange={handleChange} />
                    <button className="btn btn-outline-secondary" id="password-btn" onClick={togglePassword}>{showBtn ? "Show" : "Hide"}</button>
                    
                  </div>
                  
                  <Link className='text-danger nav-link active mb-3 text-end' to="/forget">Forget Password ?</Link>

                  
                  <div className='text-center'>
                    <button onClick={handleSubmit} className='btn btn-outline-primary w-50'>
                      Login
                    </button>
                
                  </div>
                  
                  <Link id='SignUp' className='nav-link active text-start' to={"/Register"}>SignUp</Link>
                  
                  
                </form>
              </div>
            </div>
}
        </div>
      </div>
      
    </>
  )
}
