// import { faLadderWater, faSlash } from '@fortawesome/free-solid-svg-icons';
import React from 'react';
import { useState } from 'react';
import { auth, } from "../../../config/firebase";
import { createUserWithEmailAndPassword } from "firebase/auth";
import { Link, useNavigate } from 'react-router-dom';

const initialState = {
  email: "",
  password: "",
  confirmPassword: ""
}

export default function Register() {
  const [processing, setProessing] = useState(false)
  const [state, steState] = useState(initialState);
  const navigate = useNavigate()
  const [IsPasswordShow, setIsPasswordShow] = useState(false)
  const [showBtn, setShowBtn] = useState(true)

  const handleChange = e => {
    const { name, value } = e.target
    steState(s => ({ ...s, [name]: value }))
  }
  const togglePassword = (e) => {
    e.preventDefault()
    setIsPasswordShow(!IsPasswordShow)
    setShowBtn(!showBtn)
  }


  const handleSubmit = e => {
    e.preventDefault()
    let { email, password, confirmPassword } = state;


    if (password !== confirmPassword) {
      return window.toastify("password doesnot match ", "error");
    }


    console.log(email, password)
    setProessing(true);

    createUserWithEmailAndPassword(auth, email, password)
      .then((userCredential) => {
        // Signed in 
        const user = userCredential.user;
        navigate("/")
        window.toastify("A new user has been successfully  ", "success")
        console.log(user)
        setShowBtn(showBtn);
        // ...
      })
      .catch((error) => {
        window.toastify("Something Went Wrong ", "error")

        // ..
      })
      .finally(() => {
        setProessing(false)
      })
  }

  return (
    <>

      <div className=' py-5 auth'>
        <div className='container '>
          <div className='row '>
            <div className='col'>
              <div className='card w-100 p-3 p-md-5'>
                <h1 className='text-center my-3'>
                  SignUp
                </h1>
                <form onSubmit={handleSubmit} >
                  <div className='row'>
                    <div className='col-12  mb-3'>
                      <input onChange={handleChange} className='form-control' type="Emial" placeholder='Email' name='email' />
                    </div>

                    <div className="input-group mb-3">
                      < input name='password' className='form-control p-2' type={IsPasswordShow ? "text" : "password"} placeholder='Password' id='password-btn' onChange={handleChange} />
                    </div>

                    <div className='col-12 mb-3'>
                      <div className="input-group mb-3">
                        < input name='confirmPassword' className='form-control p-2' type={IsPasswordShow ? "text" : "password"} placeholder='Confirm Password' id='password-btn' onChange={handleChange} />
                        <button className="btn btn-outline-secondary" id="password-btn" onClick={togglePassword}>{showBtn ? "Show" : "Hide"}</button>
                      </div>

                    </div>
                    <button className='btn btn-secondary' disabled={processing}>{!processing ?
                      <span>SignUp</span>
                      : <div className='spinner spinner-grow spinner-grow-sm'></div>
                    }</button>
                  </div>
                </form>
                <Link className='nav-link active mt-3 text-center w-100 ' to="/login">Already LoggedIn <span className='text-danger'>__!</span></Link>
              </div>

            </div>
          </div>
        </div>
      </div>


    </>
  )
}
