import { auth } from '../../config/firebase';
// import { AuthContext } from 'Context/AuthContext';
import { sendPasswordResetEmail } from 'firebase/auth';
import React, { useState } from 'react';
// import { useContext } from 'react';
import { Link, useNavigate } from 'react-router-dom';
// import './_auth.scss';

const initialValue = {
  email: "",
}
export default function ForgotPassword() {
  //input values
  const [value, setValue] = useState(initialValue)
  const [isloading, setLoading] = useState(false)
  //handleChange
  const handleChange = (e) => {
    setValue(s => ({ ...s, [e.target.name]: e.target.value }))
  }
  //handle submit
  const handleSubmit = (e) => {
    e.preventDefault()
    const { email } = value;
    setLoading(true)
    sendPasswordResetEmail(auth, email)
      .then((userCredential) => {
        window.toastify("Please check your email inbox", "info")
      })
      .catch((error) => {
        window.toastify(error.message, "error")
      });
    setLoading(false)

  }

  return (
    <>
      <div className="container auth my-5 ">
        <div className="row g-0 ">
          <div className="col-12 col-md-8 col-lg-6 offset-0 offset-lg-1  shadow-lg">
            <div className="card rounded-0 text-center py-5  border-0">
              <h2><b>FORGOT PASSWORD</b></h2>
              <form className='my-5 w-75 mx-auto ' onSubmit={handleSubmit} >
                <div className="inputbox   position-relative  ">
                  <input type="email" className="w-100 my-3 py-2 px-3 bg-transparent rounded-1 opacity-50 border border-secondary" name='email' onChange={handleChange} id="email" required="required" />
                </div>
                <button className="btn btn-outline-danger px-2 px-md-4 py-2 rounded-5 mt-5">
                  {isloading
                    ? <div className='spinner-border spinner-border-sm'></div>
                    : "FORGOT PASSWORD"
                  }
                </button>
              </form>
              <div className=' mt-3'>Do you know password?<Link className='text-dark fw-bold ms-2' to="/login">Login</Link></div>
            </div>
          </div>
          <div className="col-4 col-md-4 d-none d-md-block">
            <img src="https://www.teahub.io/photos/full/95-955862_hd-seat-sofa-frame-dog-vintage-wedding-photography.jpg" className='w-100' alt="" />
          </div>
        </div>
      </div>
    </>
  )
}
